import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	data() {
		return {
			list: [{
					name: '宝宝信息',
					url: 'userBabyInfo/userBabyInfo',
					imgUrl: ''
				},
				{
					name: '我的信息',
					url: 'myInformation/myInformation',
					imgUrl: ''
				},
				{
					name: '我的咨询',
					url: 'myAdvice/myAdvice',
					imgUrl: ''
				},
				{
					name: '自查记录',
					url: 'inspectionRecords/inspectionRecords',
					imgUrl: ''
				},
				{
					name: '意见反馈',
					url: 'feedback/feedback',
					imgUrl: ''
				},
			]
		}
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			// this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			this.$store.state.forcedLogin = true;
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		toUserBabyInfo(e){
			uni.navigateTo({
				url:e
			})
		}
	}
}
